.page-internal {
  padding-top: 185px;
  &__inner {
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    padding: 30px 0 40px;
  }
  &__group {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__title {
    font-size: 35px;
    line-height: 120%;
    font-weight: 800;
    color: #282828;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #282828;
    }
  }
  &__subtitle {
    font-size: 22px;
    line-height: 140%;
    font-weight: 600;
    color: #282828;
  }
  &__wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__item {
    display: flex;
    &-icon {
      flex-shrink: 0;
      width: 25px;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #ed6c1c;
      background-image: linear-gradient(180.0deg, #f6a42d -24%, #da2c20 128%);
      border-radius: 50%;
      img {
        width: 10px;
      }
    }
    &-desc {
      margin-left: 15px;
    }
    &-title {
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      color: #282828;
    }
    &-text {
      font-size: 16px;
      line-height: 140%;
      color: #282828;
      margin-top: 10px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .page-internal {
    &__inner {
      gap: 30px;
    }
    &__title {
      font-size: 28px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__subtitle {
      font-size: 14px;
    }
    &__item {
      &-icon {
        width: 20px;
        height: 20px;
      }
      &-title {
        font-size: 14px;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 940px) {
  .page-internal {
    padding-top: 225px;
    &__title {
      font-size: 25px;
    }
  }
}

@media screen and (max-width: 740px) {
  .page-internal {
    padding-top: 205px;
  }
}