.wrap {
  position: relative;
  overflow-x: hidden;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.btn-orange {
  color: #ffffff;
  background-color: #ff5700;
  background-image: linear-gradient(180.0deg, #f6a42d -25%, #da2c20 125%);
  &:hover {
    background-image: linear-gradient(180.0deg, #da2c20 -25%, #f6a42d 125%);
  }
  span {
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #ffffff;
  }
}
.btn__orange-border {
  background: transparent;
  border: 2px solid #ff5700;
  border-radius: 24.5px;
  &:hover {
    background-color: #ff5700;
    background-image: linear-gradient(180.0deg, #f6a42d -25%, #da2c20 125%);
    span {
      color: #ffffff;
    }
  }
  span {
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ff5700;
  }
}
.display2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  span:nth-child(1) {
    font-family: 'Inter';
    font-size: 53px;
    line-height: 120%;
    font-weight: 800;
    text-transform: uppercase;
    -webkit-text-stroke: 1px rgba(228, 214, 205, 0.2);
    -webkit-text-fill-color: transparent;
  }
  span:nth-child(2) {
    position: relative;
    font-size: 35px;
    line-height: 120%;
    font-weight: 800;
    text-transform: uppercase;
    color: #ffffff;
    margin-top: -30px;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: -5px;
      transform: translate(-50%, 0);
      width: 99px;
      height: 3px;
      background: #ff5700;
    }
  }
}
.display2-1 {
  span:nth-child(1) {
    -webkit-text-stroke: 1px rgba(40, 39, 40, 0.2);
  }
  span:nth-child(2) {
    color: #1c1c1c;
  }
}
.swiper-button-prev {
  width: 18px;
  height: auto;
  margin-top: 0;
  &:after {
    display: none;
  }
  img {
    width: 100%;
  }
}
.swiper-button-next {
  width: 18px;
  height: auto;
  margin-top: 0;
  &:after {
    display: none;
  }
  img {
    width: 100%;
  }
}
.swiper-pagination {
  &-bullet {
    width: 13px;
    height: 13px;
    background: transparent;
    border: 1px solid rgba(168,175,181,0.4);
    border-radius: 50%;
    opacity: 1;
  }
  &-bullet-active {
    background-color: #ff6000;
    border-color: #ff5700;
  }
}
.up {
  position: fixed;
  z-index: 6;
  right: 20px;
  bottom: -60px;
  width: 57px;
  height: 57px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #28920b;
  background-image: linear-gradient(180.0deg, #f6a42d -24%, #da2c20 126%);
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.7s;
  &--active {
    bottom: 230px;
    opacity: 1;
    pointer-events: all;
  }
  img {
    width: 16px;
  }
}
.nav {
  padding: 50px 15px;
  &-list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    li {
      font-size: 24px;
      line-height: 120%;
      color: #000000;
    }
    li a {
      &:hover {
        color: #ff5700;
      }
    }
  }
}
.breadcrumbs {
  border-bottom: 1px solid #efefef;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    position: relative;
    font-size: 15px;
    line-height: 120%;
    font-weight: 500;
    color: #ed6c1c;
    margin-bottom: 12px;
    &:not(:last-child) {
      padding-right: 16px;
      margin-right: 10px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 6px;
        height: 8px;
        background-image: url("../img/breadcrumbs-icon.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
    &--active {
      color: #282828;
    }
  }
}

@media screen and (max-width: 1150px) {
  .display2 {
    span:nth-child(1) {
      font-size: 46px;
    }
    span:nth-child(2) {
      font-size: 28px;
      margin-top: -25px;
    }
  }
  .breadcrumbs {
    &__item {
      font-size: 12px;
    }
  }
  .up {
    width: 47px;
    height: 47px;
    &--active {
      bottom: 100px;
    }
    img {
      width: 13px;
    }
  }
}

@media screen and (max-width: 940px) {
  .display2 {
    span:nth-child(1) {
      font-size: 35px;
    }
    span:nth-child(2) {
      font-size: 25px;
      margin-top: -20px;
    }
  }
}

@media screen and (max-width: 740px) {
  .display2 {
    span:nth-child(1) {
      font-size: 28px;
    }
    span:nth-child(2) {
      font-size: 22px;
      margin-top: -16px;
    }
  }
}