.main {
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 150px;
  &__img {
    img {
      width: 100%;
    }
  }
}
.section-group {
  background-image: url("../img/section-group.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.info {
  position: relative;
  z-index: 2;
  margin-top: -70px;
  &__inner {
    border: 5px solid #ffffff;
    box-shadow: 0px 26px 54px 0px rgba(0,0,0,0.07);
    border-radius: 10px;
    background: #f0f0f0;
    padding: 30px;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 30px;
    }
    &-icon {
      flex-shrink: 0;
      width: 72px;
      height: 72px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #ff5700;
      background-image: linear-gradient(180.0deg, #f6a42d -25%, #da2c20 125%);
      border-radius: 50%;
      img {
        width: auto;
        max-width: 44px;
      }
    }
    &-title {
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #222222;
      margin-left: 15px;
    }
  }
}
.purpose {
  color: #ffffff;
  padding: 60px 0 120px;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
    padding: 0 30px;
    margin-top: 50px;
  }
  &__item {
    display: flex;
    align-items: flex-end;
    &-img {
      flex-shrink: 0;
      max-width: 190px;
      img {
        width: auto;
        max-width: 100%;
      }
    }
    &-desc {
      margin-left: 20px;
    }
    &-title {
      font-size: 35px;
      line-height: 120%;
      font-weight: 800;
      color: #ffffff;
    }
    &-text {
      font-size: 16px;
      line-height: 140%;
      margin-top: 15px;
    }
  }
}
.about {
  padding: 40px 0 80px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
  }
  &__left {
    width: 555px;
    margin-right: 50px;
  }
  &__right {
    width: calc(100% - 605px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__images {
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 24px 30px;
  }
  &__img {
    position: relative;
    padding-bottom: 65%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__img--big {
    grid-row: 1/3;
    grid-column: 2;
    padding-bottom: 140%;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #191919;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 60px;
  }
  &__btn {
    width: 215px;
    height: 50px;
    border-radius: 25px;
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 15px;
    }
    span {
      font-size: 14px;
    }
  }
}
.events {
  background-image: url("../img/section-group2.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 100px 0;
  &__slider {
    position: relative;
    margin-top: 60px;
  }
  @media screen and (min-width: 741px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(2,calc((100% / 2) - (30px * 1 / 2)));
      gap: 20px 30px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next,
    .swiper-pagination {
      display: none;
    }
  }
  &__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
  }
  .swiper-button-prev {
    position: static;
  }
  .swiper-button-next {
    position: static;
  }
  .swiper-pagination {
    position: static;
    width: auto;
    margin: 0 30px;
  }
}
.event-card {
  color: #ffffff;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 87, 0, 0.6);
    box-shadow: 0px 16px 37px 3px rgba(28,6,0,0.17);
    border-radius: 20px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
  }
  &:hover {
    &:after {
      opacity: 1;
      pointer-events: all;
    }
    .event-card__date {
      i {
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
    }
    .event-card__btn {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 47%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px 0;
  }
  &__date {
    height: 21px;
    display: flex;
    align-items: center;
    padding: 0 40px;
    i {
      flex-shrink: 0;
      width: 21px;
      img {
        width: 100%;
        height: 100%;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
      margin-left: 10px;
    }
  }
  &__btn {
    width: 216px;
    height: 49px;
    background-color: #ffffff;
    box-shadow: 0px 16px 12px 1px rgba(138,47,0,0.15);
    border-radius: 24.5px;
    margin: 5px auto 0;
    opacity: 0;
    pointer-events: none;
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ff5700;
    }
  }
  &__title {
    width: 384px;
    height: 62px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ff5700;
    clip-path: polygon(0% 0%, 100% 0, 100% 0, 95% 100%, 0% 100%);
    padding: 3px 30px 0 40px;
    margin-top: 5px;
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:2;
      -webkit-box-orient: vertical;
    }
  }
}
.coaches {
  padding: 25px 0 80px;
  &__slider {
    position: relative;
    margin-top: 95px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
  }
  .swiper-button-prev {
    position: static;
  }
  .swiper-button-next {
    position: static;
  }
  .swiper-pagination {
    position: static;
    width: auto;
    margin: 0 30px;
  }
}
.coach-card {
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__top {
    max-width: 280px;
    padding-left: 30px;
  }
  &__name {
    font-size: 24px;
    line-height: 120%;
    font-weight: 800;
    text-transform: uppercase;
    color: #1c1c1c;
  }
  &__bottom {
    position: relative;
    min-height: 230px;
    display: flex;
    align-items: center;
    background-image: url("../img/coach-card__bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
    padding: 30px;
    margin-top: 25px;
  }
  &__info {
    max-width: 250px;
  }
  &__experience {
    font-size: 21px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    span {
      font-weight: 500;
    }
  }
  &__post {
    width: 100%;
    min-height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ff5700;
    border-radius: 16px;
    margin-top: 25px;
    span {
      font-size: 21px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
    }
  }
  &__img {
    position: absolute;
    right: 30px;
    bottom: 0;
    width: 230px;
    img {
      width: 100%;
    }
  }
}
.feedback {
