  background-position: center;
  background-size: cover;
  padding: 100px 0 20px 0;
  &__wrap {
    position: relative;
    display: flex;
    margin-top: 5px;
  }
  &__img {
    width: 730px;
    margin-left: -50px;
    img {
      width: 100%;
    }
  }
  &__form {
    position: absolute;
    right: 0;
    bottom: 100px;
    width: 553px;
    text-align: center;
    background: #f0f0f0;
    border: 5px solid #fff;
    box-shadow: 0px 26px 54px 0px rgba(0,0,0,0.07);
    border-radius: 10px;
    padding: 30px;
    &-title {
      font-size: 28px;
      line-height: 120%;
      font-weight: 800;
      text-transform: uppercase;
      color: #111111;
    }
    &-text {
      font-size: 15px;
      line-height: 140%;
      color: #111111;
      margin-top: 15px;
    }
    &-wrap {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      margin-top: 20px;
    }
    &-group {
      position: relative;
      border: none;
      padding: 0;
      margin: 0;
      input {
        width: 100%;
        height: 62px;
        text-align: center;
        background-color: #ffffff;
        border: 1px solid #dfdfdf;
        border-radius: 5px;
        padding: 5px 15px 5px 35px;
        font-size: 15px;
        line-height: 120%;
        color: #000000;
        &::placeholder {
          color: #9b9b9b;
        }
      }
      img {
        position: absolute;
        top: 50%;
        left: 15px;
        transform: translate(0, -50%);
        width: 15px;
      }
    }
    &-btn {
      width: 294px;
      height: 58px;
      border-radius: 28px;
      margin: 25px auto 0;
      span {
        font-size: 14px;
      }
    }
    &-info {
      position: relative;
      display: flex;
      align-items: center;
      margin-top: 25px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          &:before {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        position: relative;
        text-align: start;
        font-size: 11px;
        line-height: 150%;
        font-weight: 400;
        color: rgba(88, 88, 88, 0.6);
        padding-left: 23px;
        a {
          position: relative;
          z-index: 3;
          font-weight: 500;
          text-decoration: underline;
          color: #ff5700;
        }
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 15px;
          height: 15px;
          background: #ff5700;
          border-radius: 3px;
        }
        &:before {
          content: '';
          position: absolute;
          z-index: 1;
          top: 5px;
          left: 4px;
          width: 7px;
          height: 5px;
          background-image: url("../img/check.png");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
  }
}
