.footer {
  color: #ffffff;
  background: #000000;
  padding: 20px 0;
  margin-top: -30px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    flex-grow: 1;
    margin-left: 40px;
  }
  &__logo {
    width: 280px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__link {
    font-size: 11px;
    line-height: 120%;
    font-weight: 600;
    &:not(:first-child) {
      margin-left: 15px;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__rights {
    font-size: 12px;
    line-height: 120%;
    font-weight: 700;
    color: #9d9d9d;
    a {
      display: block;
      font-size: 11px;
    }
  }
  &__brevis {
    display: flex;
    margin-left: 30px;
    &-icon {
      width: 25px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 10px;
      line-height: 120%;
      margin-left: 10px;
      a {
        display: block;
        font-weight: 700;
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .footer {
    padding: 20px 0;
    &__right {
      flex-grow: 0;
      display: flex;
      justify-content: space-between;
    }
    &__logo {
      width: 230px;
    }
    &__list {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 10px 15px;
    }
    &__link {
      &:not(:first-child) {
        margin-left: 0;
      }
    }
    &__wrap {
      display: block;
      margin: 0 0 0 30px;
    }
    &__brevis {
      margin: 15px 0 0 0;
    }
  }
}

@media screen and (max-width: 940px) {
  .footer {
    margin-top: -40px;
    &__right {
      margin-left: 25px;
    }
    &__wrap {
      margin-left: 25px;
    }
  }
}

@media screen and (max-width: 740px) {
  .footer {
    &__inner {
      display: block;
    }
    &__right {
      display: block;
      margin: 30px 0 0 0;
    }
    &__wrap {
      margin: 30px 0 0 0;
    }
  }
}