.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  color: #ffffff;
}
.header2 {
  display: flex;
  flex-direction: column;
  .header-top {
    background: #e35224;
  }
  .header-bottom {
    order: -1;
    background: #1f1f1f;
    padding: 15px 0;
  }
  .header-top__list {
    li:hover {
      color: rgba(255, 255, 255, 0.5);
    }
  }
}
.header-top {
  background: rgba(128, 128, 128, 0.2);
  padding: 15px 0;
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      font-size: 15px;
      line-height: 120%;
      font-weight: 600;
      &:not(:first-child) {
        margin-left: 10px;
      }
      &:hover {
        color: #ff5700;
      }
    }
    li a {
      display: flex;
      align-items: center;
    }
    li span {
      display: block;
      width: 5px;
      height: 5px;
      background: rgba(255, 255, 255, 0.2);
      border-radius: 50%;
      margin-left: 10px;
    }
  }
  .header-bottom__socials {
    display: none;
  }
}
.header-bottom {
  padding-top: 25px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    margin-left: 60px;
  }
  &__logo {
    display: block;
    width: 338px;
    img {
      width: 100%;
    }
  }
  &__wrap {
    flex-grow: 1;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 15px / 2));
    gap: 15px 15px;
  }
  &__item {
    display: flex;
    align-items: center;
    i {
      width: 23px;
      height: 23px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #ff5700;
      background-image: linear-gradient(180.0deg, #f6a42d -21%, #da2c20 126%);
      border-radius: 50%;
      img {
        max-width: 13px;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
      margin-left: 8px;
    }
  }
  .mail {
    span {
      font-size: 18px;
    }
  }
  .tel {
    span {
      font-size: 18px;
      font-weight: 700;
    }
  }
  &__btns {
    margin-left: 60px;
  }
  &__btn {
    height: 36px;
    border-radius: 18px;
    padding: 3px 28px;
    span {
      font-size: 12px;
      text-transform: uppercase;
    }
  }
  &__socials {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 35px;
  }
  &__social {
    max-width: 34px;
    &:not(:first-child) {
      margin-left: 20px;
    }
    img {
      width: auto;
      max-width: 100%;
    }
  }
}
.burger {
  position: relative;
  z-index: 11;
  width: 34px;
  height: 26px;
  margin-left: 30px;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #ff5700;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span {
      background: #ffffff;
    }
    span:nth-child(1) {
      top: 12px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 12px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1150px) {
  .header-bottom {
    &__logo {
      width: 228px;
    }
    &__item {
      span {
        font-size: 11.6px;
      }
    }
    .mail,
    .tel {
      span {
        font-size: 15px;
      }
    }
    &__btn {
      padding: 3px 20px;
      span {
        font-size: 10px;
      }
    }
    &__socials {
      margin-top: 30px;
    }
    &__social {
      max-width: 28px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    &__list {
      li {
        font-size: 12px;
        &:not(:first-child) {
          margin-left: 7px;
        }
      }
      li span {
        margin-left: 7px;
      }
    }
  }
}

@media screen and (max-width: 940px) {
  .header-bottom {
    &__right {
      flex-grow: 0;
    }
    &__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 740px) {
  .header2 {
    .header-bottom {
      padding: 0;
    }
  }
  .header-top {
    position: fixed;
    z-index: 10;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    background: #000000;
    padding: 100px 15px 50px;
    overflow-y: auto;
    transition: all 0.5s;
    &--active {
      left: 0;
    }
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #ff5700;
      border-radius: 3px;
    }
    &__list {
      flex-direction: column;
      li {
        &:not(:first-child) {
          margin: 15px 0 0 0;
        }
        span {
          display: none;
        }
      }
    }
    .header-bottom__socials {
      display: flex;
      justify-content: center;
    }
  }
  .header-bottom {
    padding-top: 0;
    &__inner {
      display: block;
    }
    &__left {
      padding: 15px 0;
    }
    &__right {
      align-items: center;
      position: relative;
      padding: 15px 0;
      margin-left: 0;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -15px;
        width: calc(100% + 30px);
        height: 1px;
        background: rgba(255, 255, 255, 0.2);;
      }
      &:before {
        content: '';
        position: absolute;
        left: -15px;
        bottom: 0;
        width: calc(100% + 30px);
        height: 1px;
        background: rgba(255, 255, 255, 0.2);;
      }
    }
    &__item {
      &:nth-child(2) ~ .header-bottom__item {
        display: none;
      }
    }
    &__btns {
      margin-left: 15px;
    }
    &__socials {
      display: none;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header-bottom {
    &__wrap {
      flex-shrink: 0;
    }
    .mail,
    .tel {
      span {
        font-size: 12px;
      }
    }
  }
}